import React, { Component } from "react";
import { observer } from "mobx-react";
import Styles from "./Styles";
import TextField from "material-ui/TextField";
import FloatingActionButton from "material-ui/FloatingActionButton";
import ContentSearch from "material-ui/svg-icons/action/search";

@observer
class CellLineForm extends Component {
  render() {
    return (
      <div style={{ marginTop: "22px" }}>
        <TextField
          name="userInput"
          placeholder="CELL LINE 2"
          onChange={this.handleInput}
          underlineStyle={{ display: "none" }}
          style={Styles.input2}
        />
        <h2>GENE ANNOTATION</h2>
        <TextField
          fullWidth
          underlineStyle={{ borderColor: "#ADADAD" }}
          hintText="loss of function"
        />

        <FloatingActionButton
          style={{ float: "right", margin: "32px 16px" }}
          backgroundColor="#333">
          <ContentSearch />
        </FloatingActionButton>
      </div>
    );
  }
}
export default CellLineForm;



